"use client"

import { useState, useEffect, useRef } from "react"
import { DashboardLayout } from "@/components/layout/DashboardLayout"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { ScrollArea } from "@/components/ui/scroll-area"
import { Avatar, AvatarImage, AvatarFallback } from "@/components/ui/avatar"
import { useAuth } from "@/components/auth/AuthProvider"
import { toast } from "sonner"
import { SendIcon } from "lucide-react"
import { mockUsers } from "@/lib/auth"

interface Message {
  id: string
  senderId: string
  receiverId: string
  content: string
  timestamp: Date
  read: boolean
}

export default function MessagesPage() {
  const { user } = useAuth()
  const [messages, setMessages] = useState<Message[]>([])
  const [newMessage, setNewMessage] = useState("")
  const [selectedUser, setSelectedUser] = useState<string | null>(null)
  const scrollRef = useRef<HTMLDivElement>(null)

  // Filter users based on role
  const availableUsers = mockUsers.filter(u => 
    user?.role === 'teacher' ? u.role === 'student' : u.role === 'teacher'
  )

  useEffect(() => {
    if (user && selectedUser) {
      fetchMessages()
    }
  }, [user, selectedUser])

  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight
    }
  }, [messages])

  const fetchMessages = async () => {
    try {
      const response = await fetch(`/api/messages?userId=${user?.id}`)
      const data = await response.json()
      // Filter messages for the selected conversation
      const conversationMessages = data.filter(
        (m: Message) => 
          (m.senderId === user?.id && m.receiverId === selectedUser) ||
          (m.senderId === selectedUser && m.receiverId === user?.id)
      )
      setMessages(conversationMessages)
    } catch (error) {
      toast.error("Failed to fetch messages")
    }
  }

  const sendMessage = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!newMessage.trim() || !selectedUser || !user) return

    try {
      const response = await fetch('/api/messages', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          senderId: user.id,
          receiverId: selectedUser,
          content: newMessage
        }),
      })

      if (!response.ok) {
        throw new Error('Failed to send message')
      }

      const sentMessage = await response.json()
      setMessages(prev => [...prev, sentMessage])
      setNewMessage("")
    } catch (error) {
      toast.error("Failed to send message")
    }
  }

  const formatTime = (date: Date) => {
    return new Date(date).toLocaleTimeString([], { 
      hour: '2-digit', 
      minute: '2-digit' 
    })
  }

  return (
    <DashboardLayout>
      <div className="flex h-[calc(100vh-4rem)] gap-4">
        {/* Users list */}
        <Card className="w-64 p-4">
          <h2 className="font-semibold mb-4">
            {user?.role === 'teacher' ? 'Students' : 'Teachers'}
          </h2>
          <ScrollArea className="h-full">
            <div className="space-y-2">
              {availableUsers.map((u) => (
                <div
                  key={u.id}
                  className={`flex items-center gap-3 p-2 rounded-lg cursor-pointer hover:bg-gray-100 ${
                    selectedUser === u.id ? 'bg-gray-100' : ''
                  }`}
                  onClick={() => setSelectedUser(u.id)}
                >
                  <Avatar>
                    <AvatarImage src={u.image} alt={u.name} />
                    <AvatarFallback>{u.name[0]}</AvatarFallback>
                  </Avatar>
                  <div>
                    <p className="font-medium">{u.name}</p>
                    <p className="text-sm text-gray-500">{u.role}</p>
                  </div>
                </div>
              ))}
            </div>
          </ScrollArea>
        </Card>

        {/* Chat area */}
        <Card className="flex-1 p-4 flex flex-col">
          {selectedUser ? (
            <>
              <div className="mb-4 pb-4 border-b">
                <h2 className="font-semibold">
                  {availableUsers.find(u => u.id === selectedUser)?.name}
                </h2>
              </div>

              <ScrollArea ref={scrollRef} className="flex-1 pr-4">
                <div className="space-y-4">
                  {messages.map((message) => {
                    const isOwn = message.senderId === user?.id
                    return (
                      <div
                        key={message.id}
                        className={`flex ${isOwn ? 'justify-end' : 'justify-start'}`}
                      >
                        <div
                          className={`max-w-[70%] rounded-lg p-3 ${
                            isOwn 
                              ? 'bg-blue-500 text-white' 
                              : 'bg-gray-100 text-gray-900'
                          }`}
                        >
                          <p>{message.content}</p>
                          <p className={`text-xs mt-1 ${
                            isOwn ? 'text-blue-100' : 'text-gray-500'
                          }`}>
                            {formatTime(message.timestamp)}
                          </p>
                        </div>
                      </div>
                    )
                  })}
                </div>
              </ScrollArea>

              <form onSubmit={sendMessage} className="mt-4 flex gap-2">
                <Input
                  value={newMessage}
                  onChange={(e) => setNewMessage(e.target.value)}
                  placeholder="Type your message..."
                  className="flex-1"
                />
                <Button type="submit" disabled={!newMessage.trim()}>
                  <SendIcon className="h-4 w-4" />
                </Button>
              </form>
            </>
          ) : (
            <div className="h-full flex items-center justify-center text-gray-500">
              Select a user to start messaging
            </div>
          )}
        </Card>
      </div>
    </DashboardLayout>
  )
}
